@media (max-width: 500px) {
    .ribbon {
        width: 100%;
    }

    .ribbon:nth-child(even) {
        margin-right: 0%;
    }
}

.ribbon1 {
    position: absolute;
    top: -6.1px;
    right: 10px;
}

.ribbon1:after {
    position: absolute;
    content: "";
    width: 0;
    height: 0;
    border-left: 53px solid transparent;
    border-right: 53px solid transparent;
    border-top: 10px solid #f8463f;
}

.ribbon1 span {
    position: relative;
    display: block;
    text-align: center;
    background: #f8463f;
    font-size: 14px;
    line-height: 1;
    padding: 12px 8px 10px;
    border-top-right-radius: 8px;
    width: 90px;
}

.ribbon1 span:before,
.ribbon1 span:after {
    position: absolute;
    content: "";
}

.ribbon1 span:before {
    height: 6px;
    width: 6px;
    left: -6px;
    top: 0;
    background: #f8463f;
}

.ribbon1 span:after {
    height: 6px;
    width: 8px;
    left: -8px;
    top: 0;
    border-radius: 8px 8px 0 0;
    background: #c02031;
}

.ribbon2 {
    width: 60px;
    padding: 10px 0;
    position: absolute;
    top: -6px;
    left: 25px;
    text-align: center;
    border-top-left-radius: 3px;
    background: #f47530;
}

.ribbon2:before {
    height: 0;
    width: 0;
    right: -5.5px;
    top: 0.1px;
    border-bottom: 6px solid #8d5a20;
    border-right: 6px solid transparent;
}

.ribbon2:before,
.ribbon2:after {
    content: "";
    position: absolute;
}

.ribbon2:after {
    height: 0;
    width: 0;
    bottom: -29.5px;
    left: 0;
    border-left: 30px solid #f47530;
    border-right: 30px solid #f47530;
    border-bottom: 30px solid transparent;
}

.ribbon3 {
    font-size: smaller;
    width: 90px;
    height: 28px;
    line-height: 30px;
    padding-left: 15px;
    position: absolute;
    left: -8px;
    top: 60px;
    background: #57dd43;
}

.ribbon3:before,
.ribbon3:after {
    content: "";
    position: absolute;
}

.ribbon3:before {
    height: 0;
    width: 0;
    top: -8.5px;
    left: 0.1px;
    border-bottom: 9px solid black;
    border-left: 9px solid transparent;
}

.ribbon3:after {
    height: 0;
    width: 0;
    right: -14.5px;
    border-top: 9px solid transparent;
    border-bottom: 19px solid transparent;
    border-left: 15px solid #57dd43;
}

.ribbon4 {
    position: absolute;
    top: 15px;
    padding: 8px 10px;
    background: #00b3ed;
    box-shadow: -1px 2px 3px rgba(0, 0, 0, 0.3);
}

.ribbon4:before,
.ribbon4:after {
    content: "";
    position: absolute;
}

.ribbon4:before {
    width: 7px;
    height: 100%;
    top: 0;
    left: -6.5px;
    padding: 0 0 7px;
    background: inherit;
    border-radius: 5px 0 0 5px;
}

.ribbon4:after {
    width: 5px;
    height: 5px;
    bottom: -5px;
    left: -4.5px;
    background: lightblue;
    border-radius: 5px 0 0 5px;
}

.ribbon5 {
    display: block;
    width: calc(100% + 20px);
    height: 50px;
    line-height: 50px;
    text-align: center;
    margin-left: -10px;
    margin-right: -10px;
    background: #edba19;
    position: relative;
    top: 20px;
}

.ribbon5:before,
.ribbon5:after {
    content: "";
    position: absolute;
}

.ribbon5:before {
    height: 0;
    width: 0;
    bottom: -10px;
    left: 0;
    border-top: 10px solid #cd8d11;
    border-left: 10px solid transparent;
}

.ribbon5:after {
    height: 0;
    width: 0;
    right: 0;
    bottom: -10px;
    border-top: 10px solid #cd8d11;
    border-right: 10px solid transparent;
}

.wrap {
    width: 100%;
    height: 188px;
    position: absolute;
    top: -8px;
    left: 8px;
    overflow: hidden;
}

.wrap:before,
.wrap:after {
    content: "";
    position: absolute;
}

.wrap:before {
    width: 40px;
    height: 8px;
    right: 100px;
    background: #4d6530;
    border-radius: 8px 8px 0px 0px;
}

.wrap:after {
    width: 8px;
    height: 40px;
    right: 0px;
    top: 100px;
    background: #4d6530;
    border-radius: 0px 8px 8px 0px;
}

.ribbon6 {
    width: 200px;
    height: 40px;
    line-height: 40px;
    position: absolute;
    top: 30px;
    right: -50px;
    z-index: 2;
    overflow: hidden;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    border: 1px dashed;
    box-shadow: 0 0 0 3px #57dd43, 0px 21px 5px -18px rgba(0, 0, 0, 0.6);
    background: #57dd43;
    text-align: center;
}