span,
a,
a:hover {
    display: inline-block;
    text-decoration: none;
    color: inherit;
}

.blog img {
    max-width: 100% !important;
}

.blog-head {
    font-size: xx-large !important;
    margin-bottom: 10px !important;
    margin-top: 0px !important;
}

.blog-head h6 {
    color: #f05907 !important;
    position: relative !important;
    display: inline-block !important;
    text-transform: capitalize !important;
    margin: 0px !important;
}

.blog-head h6:after,
.blog-head h6:before {
    position: absolute !important;
    content: "" !important;
    width: 50px !important;
    height: 3px !important;
    background: #f05907 !important;
    top: 50% !important;
}

.blog-head h6:after {
    right: 120% !important;
}

.blog-head h6:before {
    left: 120% !important;
}

.overlay {
    position: absolute !important;
    top: 0 !important;
    left: 0 !important;
    width: 100% !important;
    height: 100% !important;
    background-color: rgba(0, 0, 0, 0.5) !important;
    z-index: 0 !important;
}

.blog {
    text-align: center;
    background-color: #f7f7f7 !important;
    /* padding: 100px 0 !important; */
    min-height: 100vh !important;
}

.blog .item {
    background-color: #fff !important;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1) !important;
    border-radius: 10px !important;
    overflow: hidden !important;
    position: relative !important;
}

.blog .item .more {
    position: absolute !important;
    right: 30px !important;
    bottom: 20px !important;
    box-shadow: 0 0 30px rgba(0, 0, 0, 0.1) !important;
    color: #f05907 !important;
    font-size: 19px !important;
    width: 40px !important;
    height: 40px !important;
    border-radius: 50% !important;
    line-height: 40px !important;
    text-align: center !important;
    transform: translate(0, 100px) !important;
    transition: all 0.3s ease-in-out !important;
}

.blog .item:hover .more {
    transform: translate(0, 0) !important;
}

.blog .item:hover .img img {
    transition: all 0.4s ease !important;
}

.blog .item:hover .img img {
    transform: rotate(-5deg) scale(1.2, 1.2) !important;
}

.blog .item .img {
    clip-path: polygon(0 0, 100% 0, 100% 70%, 0 100%, 0 75%) !important;
}

.blog .item .info {
    padding: 30px !important;
    position: relative !important;
}

.blog .item .info .date {
    position: absolute !important;
    left: calc(50% - 25px) !important;
    top: -54px !important;
    width: 50px !important;
    height: 50px !important;
    line-height: 20px !important;
    text-align: center !important;
    background-color: #f05907 !important;
    color: #fff !important;
    padding: 5px !important;
    transform: rotate(45deg) !important;
}

.blog .item .info .date span {
    transform: rotate(-45deg) !important;
    display: inline-block !important;
}

.blog .item .info h5:hover {
    color: #f05907 !important;
}

.blog .item .info .user {
    margin-top: 20px !important;
    color: #f05907 !important;
}

.blog .item .info .user i {
    margin-right: 5px !important;
    font-size: 14px !important;
}